<template>
  <div style="width:95%;margin:0 auto">
    <el-divider></el-divider>
    <el-card class="">
      <el-form :model="selectForm">
        <el-row :gutter="10">
          <el-col :span="8">
            <el-form-item label="赛季(懒加载):" >
              <el-select
                v-model="selectForm.season"
                remote
                filterable
                clearable
                :remote-method="remoteMethod"
                v-el-select-loadmore="loadMore()">
                <el-option
                  v-for="item in seasonArray.slice(0,rangeNumber)"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="阶乘:" label-width="50px" style="width:90%">
              <el-input
                placeholder="请输入"
                clearable
                v-model="selectForm.factorial"
                onkeyup="value=value.replace(/[^\d]/g,'')"
                @keyup.enter.native="mathFactorial">
              </el-input>
            </el-form-item>
            <span style="font-size:14px;color:red">阶乘的值为：{{factorialNum}}</span>
          </el-col>
          <el-col :span="10">
            <el-form-item label="生产时间:">
              <el-date-picker
                v-model="produceTimeRange"
                type="datetimerange"
                range-separator="至"
                value-format="yyyy-MM-dd HH:mm:ss"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- 搜索 -->
        <el-row>
          <el-col :span="24">
            <div style="float:right">
              <el-button type="primary" size="mini" @click="showViewsJSON">ViewsJSON</el-button>
              <el-button type="primary" size="mini" @click="showViewerJSON">ViewerJSON</el-button>
              <el-button type="primary" size="small" @click="handleSearch" v-throttle>搜索</el-button>
              <el-button type="primary" size="small">重置</el-button>
            </div>
          </el-col>
        </el-row>
      </el-form>
      <!-- Json -->
      <div class="json-view">
        <jsonViews :data="workJson" v-show="activeViews"></jsonViews>
        <JsonViewer copyable :expand-depth="5" :value="workJson" v-show="activeViewer"></JsonViewer>
      </div>
    </el-card>
  </div>
</template>

<script>
//自定义懒加载指令
// import Vue from "vue"
// Vue.directive(
//   'el-select-loadmore', {
//     bind(el, binding) {
//       //获取element-ui定义好的scroll盒子
//       const SELECTWRAP_DOM = el.querySelector('.el-select-dropdown .el-select-dropdown__wrap')
//       SELECTWRAP_DOM.addEventListener('scroll', function () {
//         const condition = this.scrollHeight - this.scrollTop <= this.clientHeight
//         if (condition) binding.value()
//       })
//     }
//   }
//
import {workJsons} from "@/common/js/workJson"
import jsonViews from 'vue-json-views' //引入jsonViews
import JsonViewer from 'vue-json-viewer' //引入JsonViewer
export default {
  name:"workbench",
  components:{
    jsonViews,
    JsonViewer
  },
  data(){
    return{
      workJson:null,
      activeViews:true,
      activeViewer:false,
      selectForm:{
        season:"",

      },
      factorialNum:"",
      seasonArray:[],
      rangeNumber:10, //默认显示条数
      produceTimeRange:[]
    }
  },
  watch:{
    "selectForm.factorial":{
      handler(newVal,oldVal){
        if(newVal === ""){
          this.factorialNum = ""
        }
      }
    }
  },
  created(){
    // let nowTimeDate = new Date();
    // let nowTime = nowTimeDate.toLocaleString()  //当前时间  '2022/7/25 15:53:36'
    // let realTime = nowTime.slice(0,5) + "0" + nowTime.slice(5)   // 当前时间  '2022/07/25 15:53:36'
    // let now = realTime.replace(/\//g,"-");  //  转义/并替换为-     '2022-07-25 15:53:36'
    // console.log(nowTime,typeof nowTime === "string");
    // console.log(111,nowTime,now);

    this.workJson = workJsons
  },
  methods:{
    //远程搜索
    remoteMethod(text){
      // console.log(text);
      //模拟
      this.seasonArray = [] //避免累加
      for(let i=0;i<20000;i++){
        this.seasonArray.push({value:i,label:"赛季"+i})
      }

      //真实
      // let params = {
      //       seasonText:text
      //     }
      // get_season_list(params).then(res=>{
      //   if(res.data.code ==="0"){
      //     this.seasonArray = res.data.result;
      //   }else{
      //     this.$message.error(res.data.msg)
      //   }
      // }).catch(err=>{
      //   this.$message.error(err)
      // })
    },
    //懒加载
    loadMore(){
      return ()=> this.rangeNumber += 5 //每次增加5条,根据业务每次增加适当数量
    },
    handleSearch(){
      let params = {
        season:this.selectForm.season,
        produceStartTime: this.produceTimeRange && this.produceTimeRange.length > 0 ?this.produceTimeRange[0] :"", //开始时间
        produceEndTime: this.produceTimeRange && this.produceTimeRange.length > 0 ?this.produceTimeRange[1] :"", //结束时间
      }
      // console.log("params",params);
      // console.log(11,this.produceTimeRange[0],typeof this.produceTimeRange[0]);
      console.log("自定义节流");
    },
    showViewsJSON(){
      if(this.workJson === null || this.workJson === ""){
        this.activeViews = false
        this.activeViewer = false
        return false
      }
      this.activeViews = true
      this.activeViewer = false
    },
    showViewerJSON(){
       if(this.workJson === null || this.workJson === ""){
        this.activeViews = false
        this.activeViewer = false
        return false
      }
      this.activeViewer = true
      this.activeViews = false
    },
    //计算阶乘  缺校验负数
    mathFactorial(){
      function factorial(num){
        if(num <= 1){ //递归出口
          return 1
        }else{
          return num * factorial(num - 1)
        }
      }
      //调用函数赋值
      this.factorialNum = factorial(this.selectForm.factorial)
    }
  }
}
</script>

<style lang="less" scoped>
.json-view{
  width: 90%;
  border:1px solid red;
  margin: 20px auto;
  min-height: 200px;
  max-height: 500px;
  overflow-x:hidden;
  overflow-y: scroll;
}
</style>



